import { Component, OnInit, ViewChild, ChangeDetectorRef, ElementRef } from '@angular/core';
import {AdminService} from '../../services/admin/admin.service' ;
import {AuthService} from '../../services/auth/auth.service'
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { environment } from '../../../environments/environment' ;
import {FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { FileUploader } from 'ng2-file-upload';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';


const uri = environment.uri;


@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class feedbackComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild('closeModal', {static: true} ) closeModal: ElementRef;
  uploader: FileUploader = new FileUploader({url: uri, removeAfterUpload: true , allowedFileType: ['image', 'doc', 'pdf', 'xls', 'ppt']});
  filePreviewPath: SafeUrl;
  hasBaseDropZoneOver:boolean = false;
  hasAnotherDropZoneOver:boolean = false;
  submited = false;
  removing = false;
  formclinet : FormGroup;
  formreponse : FormGroup
  userfeedback = [] ;
  uri = environment.baseUrl;
  mypermissions: any[];
  MyDataSourceclient : any
  clientdatasource : any
  selectedfeedback : any 
  message = []
  displayedColumns: string[] = ['Libelle','Description','Date','Etat'];
  docList = [] ;

confirmationId :string ;

  constructor(
              private adminService: AdminService,
              private AuthService : AuthService,
              private fb: FormBuilder,
              private changeDetectorRef: ChangeDetectorRef,
              private sanitizer: DomSanitizer,          
              private snackBar: MatSnackBar,) { 
    
               
                this.uploader.onBeforeUploadItem = (item) => {
                  item.withCredentials = false;             
                 }
                  this.uploader.onAfterAddingFile = (fileItem) => {
                    this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
                   }
                
                   this.formclinet = this.fb.group({
                    Sujet: ['', Validators.required],
                    descriptionsujet: '',
                   
                   })
                   this.formreponse = this.fb.group({
                    reponsesujet : ['' ,Validators.required ]
                   })
                
              }

  ngOnInit() {
  this.fetchuserfeedbackData();
  }
  
  public fileOverBase(e:any):void {
    this.hasBaseDropZoneOver = e;
  }
 
  public fileOverAnother(e:any):void {
    this.hasAnotherDropZoneOver = e;
  }
  rotateArrow(section,action){
    let files : HTMLElement = document.getElementById(section) as HTMLElement;
    if(!files.classList.contains('down')){
      files.classList.add('down') ;
    }else{
      files.classList.remove('down');
    }
  }

  removeFile(item){
    this.removing = true;
    item.remove();
    setTimeout(()=>{    
      this.removing = false;
    }, 500); 
    this.changeDetectorRef.detectChanges();
  }


  fetchuserfeedbackData(){
  const id = this.AuthService.getIdfromToken()
    this.adminService.getFeedbacksclient()
        .subscribe(res => { 
      
  res = res.filter(data => data.ClineId == id)
          
        this.MyDataSourceclient = new MatTableDataSource();
        this.MyDataSourceclient.data = res;
        this.MyDataSourceclient.paginator = this.paginator;
        this.MyDataSourceclient.sort = this.sort;
  }, err => {
    console.log(err);
  });

  }

  detailmessage(message) {
  
  this.selectedfeedback = message._id;
    this.message = message.reponse
    console.log(this.message)
  }



    applyFilter(filterValue: string) {
      this.MyDataSourceclient.filter = filterValue.trim().toLowerCase();
    }
    onSubmitclientfeedback(){
      console.log(this.formclinet.value)
      if(this.formclinet.valid){
        this.submited = true;
        if (this.uploader.queue.length > 0) {
          for(var i =0;i<this.uploader.queue.length;i++){
          this.uploader.queue[i].upload()
          this.uploader.onCompleteItem = (item: any, response: any , status: any, headers: any) => {
            if(response)
            this.docList.push(JSON.parse(response).uploadname);
            else{
              this.snackBar.open('Une erreur c\'est produit verifier votre connection', 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack-error'],
               });
            }
          }
        }
      }
  
        let feedback = this.formclinet.value;
        if(this.uploader.queue.length > 0){
          this.uploader.onCompleteAll = () => {
            feedback.files = this.docList;
            this.adminService.addFeedbackclient(feedback).subscribe(data => {
              if (data.success) {
                this.fetchuserfeedbackData();
                this.snackBar.open(data.msg, 'X', {
                   duration: 6000,
                   verticalPosition: 'top',
                   panelClass:['custom-snack'],
                 });
                this.closeModal.nativeElement.click() //<-- here
                this.formclinet.reset();
              } else {
                this.snackBar.open(data.msg, 'X', {
                 duration: 6000,
                 verticalPosition: 'top',
                 panelClass:['custom-snack-error'],
               });
              }
              this.submited = false;
             });
          }
        }else{
        this.adminService.addFeedbackclient(feedback).subscribe(data => {
          if (data.success) {    
            this.fetchuserfeedbackData();
            this.snackBar.open(data.msg, 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack'],
           });
            this.closeModal.nativeElement.click() //<-- here
            this.formclinet.reset();
          } else {
            this.snackBar.open(data.msg, 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack-error'],
           });
          }
          this.submited = false;
         });
      } }
      else{
        this.snackBar.open('Verifier les champs', 'X', {
             duration: 6000,
             verticalPosition: 'top',
             panelClass:['custom-snack'],
           });
        this.submited = false;
      }
  }
  delete(id){
    this.adminService.deleteFeedbacksclient(id).subscribe(data => {  
      if (data.success) {    
      this.fetchuserfeedbackData();
      this.snackBar.open(data.msg, 'X', {
       duration: 6000,
       verticalPosition: 'top',
       panelClass:['custom-snack'],
     });
   
    }
  })
}
onSubmitreponse(){
  console.log(this.formreponse.value)
  let reponse = {
    reponse :this.formreponse.controls.reponsesujet.value,
    date : new Date()

  }
  this.adminService.addreponseFeedbacksclient(this.selectedfeedback ,reponse).subscribe( data => {
    if (data.success) {    
      this.fetchuserfeedbackData();
      this.snackBar.open(data.msg, 'X', {
       duration: 6000,
       verticalPosition: 'top',
       panelClass:['custom-snack'],
     });
      this.closeModal.nativeElement.click() //<-- here
      this.formreponse.reset();
    } else {
      this.snackBar.open(data.msg, 'X', {
       duration: 6000,
       verticalPosition: 'top',
       panelClass:['custom-snack-error'],
     });
    }
  })

  
}
}
